import React, { Component } from 'react';
import './examples.css';
import LZString from 'lz-string'
import exampleData from './examplesData';
import LazyLoad from 'react-lazyload';
import placeholder from "./loading.gif"

export default class Examples extends Component {
    constructor(props){
        super(props)
        this.state={
            data_temp: exampleData
        }
    }
    exampleLaunch=(specJson)=>{
        let compressResult = LZString.compressToBase64(JSON.stringify(specJson));
        window.location.href="https://narchart.github.io/editor/#/url/"+compressResult;
        window.location.reload()
    }  
    render() {
        const {data_temp} = this.state;
        let count=0;
        // let urltemp=  require("./examplesImg/barchart0.jpg");
        let holderImg = <img src={placeholder} width={"200px"} height={"200px"} alt="loading"/>
        return( 
            <div className="gallery-Container">
                {  
                ["Bar Chart","Line Chart","Pie Chart", "Scatterplot","Unit"].map((item)=>{
                    return(
                        <div key={item+count}>
                        <li className="chart-description-classify">{item}</li>
                        <div className="charts-gallery" key={"gallery"+count}>
                            {data_temp.filter(d=>d.chartType===item).map((d,index)=>{
                                let url=  require("./examplesImg/"+d.chartType.replace(/\s*/g,"").toLowerCase()+count+".gif");
                                count++;
                                return(
                                    <div className="chart-gallery-grid" key={"example"+count}>
                                        <div style={{width:"170px", height:"170px"}}>
                                        <LazyLoad key={index} scrollContainer='.gallery-Container' scroll={true} offset={170} height={170} placeholder={holderImg}>
                                            <img src={url} alt="" onClick={()=>this.exampleLaunch(d.specJson)}/>
                                        </LazyLoad>
                                        </div>
                                        <div className="galary-description">
                                            <div className="galary-word" title={"example"+index}>{"example"+(count)}</div>
                                            <div className="galary-button"> 
                                                <button id="gallery-launch" onClick={()=>this.exampleLaunch(d.specJson)}>Launch</button>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}
                             <i></i><i></i><i></i><i></i><i></i>
                        </div>
                        </div>
                    )
                })}
                
                
                </div>
        )
    }
}