import React, { Component } from 'react';
// import { NarrativeChart } from '../vis';
import {NarrativeChart} from 'narchart'
import './playground.css';
import _ from 'lodash';

export default class Chart extends Component {


    componentDidMount() {
        let spec = this.props.spec;
        let container = "#demo-chart";
        this.vis = new NarrativeChart();
        this.vis.container(container);
        this.vis.generate(spec);
    }
    shouldComponentUpdate(nextProps){
        let {spec, clickRun} = this.props;
        let nextSpec = nextProps.spec;
        let nextClickRun = nextProps.clickRun
        return !_.isEqual(spec, nextSpec)||!_.isEqual(clickRun, nextClickRun);
    }
    componentDidUpdate() {
        let container = "#demo-chart";
        let spec = this.props.spec;
        this.vis.container(container);
        this.vis.stop()
        this.vis.generate(spec);
    }

    render() {
        let height = 640, width = 640;
        if (this.props.spec.chart) {
            let size = this.props.spec.chart.size;
            switch (size) {
                case 'wide':
                    height = 220;
                    width = 560;
                    break;
                case 'middle':
                    height = 200;
                    width = 360;
                    break;
                case 'small':
                    height = 150;
                    width = 235;
                    break;
                case 'unitvis':
                    height = 720;
                    width = 680;
                    break;               
                default:
                    break;
            }
        }
        // <div id='frame' style={{ marginLeft: 30, marginTop: 30, maxHeight: height, maxWidth: width, borderStyle: 'solid', borderWidth: 1, borderColor: '#CCCCCD' }}>

        return (
            <div id='frame' style={{ margin:"auto",marginTop: 30, maxHeight: height+2, maxWidth: width+2, borderStyle: 'solid', borderWidth: 1, borderColor: '#CCCCCD' }}>

                <div id='demo-chart'
                    style={{
                        height: height,
                        width: width,
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        // justifyContent: "center"
                    }} />
            </div>
        )
    }
}
